<template>
  <div class="body">
    <nav class="navbar">
      <div class="tab interface" id="edit_to_list">
        <a @click="selectTab({ no: 'main' })" title="Главня">
          <img src="./../../assets/images/interface/main.png" alt="" />
        </a>
      </div>
      <div class="tab interface" id="edit_to_list">
        <a
          @click="
            prevCatalog = true;
            add = 0;
            catalogMode = true;
          "
          title="Каталог"
        >
          <img src="./../../assets/images/interface/catalog.png" alt="" />
        </a>
      </div>
      <!-- <div class="tab interface" id="edit_to_list">
        <router-link :to="{ name: 'ScriptMain' }" title="Скрипты">
          <img src="./../../assets/images/interface/script.png" alt="" />
        </router-link>
      </div> -->
      <div class="tab interface" id="edit_to_list">
        <a @click="addTab('new', {})" title="Добавить заявку">
          <img src="./../../assets/images/interface/plus.png" alt="" />
        </a>
      </div>
      <div class="allbar">
        <nav-tab
          v-for="tab in tabs"
          :key="tab.no"
          @close="tabClose(tab)"
          @click="selectTab(tab)"
          v-bind:data="tab"
          class="tab"
          :class="{ selected: tab.no == selectedTab }"
        ></nav-tab>
      </div>
      <div class="tab interface" id="edit_to_list">
        <a @click="exit()" title="Выйти из аккаунта">
          <img src="./../../assets/images/interface/logout.png" alt="" />
        </a>
      </div>
      <div class="tab interface" id="edit_to_list">
        <a @click="showMenu = !showMenu" title="Меню">
          <img src="./../../assets/images/interface/burger.png" alt="" />
        </a>
      </div>
    </nav>

    <nav v-show="showMenu" class="menu">
      <router-link v-if="user.admin" to="/admin"
        >Панель администратора</router-link
      >
      <router-link v-if="schedules()" to="/schedules">Графики</router-link>
    </nav>
    <div class="leftbar" :class="{ hidden: displayMain }">
      <!-- 
      <button
        id="seeing_catalog"
        @click="
          prevCatalog = true;
          add = 0;
          catalogMode = false;
        "
        class="button border-2g"
      >
        Товары на складе
      </button> -->
      <div class="sorting">
        <sort-main @sorting="updateMain"></sort-main>
      </div>
    </div>

    <main v-if="displayMain" id="main">
      <table>
        <tr class="header">
          <th>№</th>
          <th @click="sortingFor($event, 'city')" data-order="none">Город</th>
          <th>Адрес</th>
          <th>№ 1С</th>
          <th>Способ оплаты</th>
          <th style="min-width: 9em">Номер</th>
          <th @click="sortingFor($event, 'status')" data-order="none">
            Статус
          </th>
          <th>Действие</th>
          <th @click="sortingFor($event, 'date')" data-order="none">
            Дата действия
          </th>
          <th>Время действия</th>
          <th>Заметка</th>
        </tr>
        <tr
          @click="addTab(data.no, data)"
          v-for="data in mainList.slice(0, count)"
          :key="data.no"
        >
          <td>{{ data.no }}</td>
          <td>{{ data.city }}</td>
          <td>
            {{ data.street ? data.street + ", д." + data.house : "" }}
            <span class="null" v-show="!data.street">----------</span>
          </td>
          <td>
            {{ data.no_1c }}
            <span class="null" v-show="!data.no_1c">----------</span>
          </td>
          <td>
            {{ data.payment_method }}
            <span class="null" v-show="!data.payment_method">----------</span>
          </td>
          <td>
            {{ data.number ? $acceptNumber(data.number) : "" }}
            <span class="null" v-show="!data.number">----------</span>
          </td>
          <td>{{ data.status }}</td>
          <td>
            {{
              data.status == "Замер"
                ? data.measuring_date
                  ? "Замер"
                  : ""
                : data.status == "Доставка" || data.status == "Завершённый"
                ? data.delivery_date
                  ? "Доставка"
                  : ""
                : data.expectation
                ? "Связаться повторно"
                : ""
            }}<span
              class="null"
              v-show="
                !(data.status == 'Замер'
                  ? data.measuring_date
                    ? 'Замер'
                    : ''
                  : data.status == 'Доставка' || data.status == 'Завершённый'
                  ? data.delivery_date
                    ? 'Доставка'
                    : ''
                  : data.expectation
                  ? 'Связаться повторно'
                  : '')
              "
              >----------</span
            >
          </td>

          <td>
            {{
              data.status == "Замер"
                ? $getStringDate(data.measuring_date)
                : data.status == "Доставка" || data.status == "Завершённый"
                ? $getStringDate(data.delivery_date)
                : $getStringDate(data.expectation)
            }}
            <span
              v-show="
                !(data.status == 'Замер'
                  ? $getStringDate(data.measuring_date)
                  : data.status == 'Доставка' || data.status == 'Завершённый'
                  ? $getStringDate(data.delivery_date)
                  : $getStringDate(data.expectation))
              "
              class="null"
              >----------</span
            >
          </td>
          <td>
            {{
              data.status == "Замер"
                ? $getTime(data.measuring_date, data.measuring_time)
                : data.status == "Доставка" || data.status == "Завершённый"
                ? $getTime(data.delivery_date, data.delivery_time)
                : ""
            }}
            <span
              v-show="
                !(data.status == 'Замер'
                  ? $getTime(data.measuring_date, data.measuring_time)
                  : data.status == 'Доставка' || data.status == 'Завершённый'
                  ? $getTime(data.delivery_date, data.delivery_time)
                  : '')
              "
              class="null"
              >----------</span
            >
          </td>
          <td>
            <pre>{{ data.note }}</pre>
          </td>
        </tr>
      </table>
      <button
        v-if="mainList.length >= count"
        class="button"
        @click="count += 40"
      >
        Показать еще
      </button>
    </main>

    <application-body
      v-else
      :key="dataPage.no + Math.random()"
      :dataPage="dataPage"
    ></application-body>

    <catalog-body
      @close="prevCatalog = false"
      @addProduct="addProduct"
      :add="add"
      :catalog="catalogMode"
      v-if="prevCatalog"
    ></catalog-body>
  </div>
</template>

<script>
import CatalogBody from "./../Catalog/CatalogBody.vue";
import ApplicationBody from "./../Application/ApplicationBody.vue";
import NavTab from "./NavTab.vue";
import SortMain from "./SortMain.vue";

export default {
  components: {
    NavTab,
    CatalogBody,
    ApplicationBody,
    SortMain,
  },
  data() {
    return {
      showMenu: false,
      user: {},
      add: false,
      // cites: [],
      // toc: [],
      status: [],
      mainList: [],
      selectedTab: "main",
      prevCatalog: false,
      displayMain: true,
      dataPage: {},
      tabs: [],
      sortMainData: [],
      catalogMode: true,
      count: 40,
    };
  },
  methods: {
    schedules() {
      return this.user.rights.includes("11") || this.user.rights.includes("12");
    },
    async exit() {
      localStorage.removeItem("user");
      await fetch(this.$connect + "exit.php");
      this.$router.push("login");
    },
    async searchMain(data) {
      if (data.text == "") {
        data = [];
      }
      const f = await fetch(this.$connect + "mainList.php", {
        method: "POST",
        body: JSON.stringify(data),
      });
      const text = await f.json();
      text.forEach((el) => {
        this.mainList.push(el);
      });
    },
    async getLists() {
      const data = await fetch(
        this.$connect + "application/getLists.php?list=0"
      ).then((resp) => resp.json());
      this.status = data.status;
    },
    tabClose(tabToClose) {
      if (tabToClose.data.seved) {
        this.tabs = this.tabs.filter((tab) => tab != tabToClose);
        this.updateMain(this.sortMainData);
        this.dataPage = {};
      } else {
        let res = confirm(
          "Заявка не была сохранена на сервере, вы действительно хотите закрыйть ее? В случае закрытия данные будут утеряны."
        );
        if (res) {
          this.tabs = this.tabs.filter((tab) => tab != tabToClose);
          this.updateMain(this.sortMainData);
          this.dataPage = {};
        } else {
          return false;
        }
      }
      this.selectedTab == tabToClose.no
        ? this.selectTab({ no: "main" })
        : false;
    },
    async addProduct(id, type) {
      this.dataPage.seved = false;
      let list = this.dataPage.productList;
      let newKey = 1;
      switch (type) {
        case "Linoleum":
          list.linoleum.push(
            await fetch(
              this.$connect +
                "application/dataProduct.php?id_linoleum=" +
                id +
                "&key=" +
                this.dataPage.lastKey
            ).then((resp) => resp.json())
          );
          this.dataPage.lastKey++;
          break;
        case "Plintusa":
          list.plinth.push(
            await fetch(
              this.$connect +
                "application/dataProduct.php?id_plinth=" +
                id +
                "&key=" +
                this.dataPage.lastKey
            ).then((resp) => resp.json())
          );
          this.dataPage.lastKey++;
          break;
        case "accessories":
          list.accessories.push(
            await fetch(
              this.$connect +
                "application/dataProduct.php?id_accessories=" +
                id +
                "&key=" +
                this.dataPage.lastKey
            ).then((resp) => resp.json())
          );
          this.dataPage.lastKey++;
          break;
        case "Porogi":
          list.doorstep.push(
            await fetch(
              this.$connect +
                "application/dataProduct.php?id_doorstep=" +
                id +
                "&key=" +
                this.dataPage.lastKey
            ).then((resp) => resp.json())
          );
          this.dataPage.lastKey++;
          break;
        case "Sop. Tovary": {
          list.related.push(
            await fetch(
              this.$connect +
                "application/dataProduct.php?id_related=" +
                id +
                "&key=" +
                this.dataPage.lastKey
            ).then((resp) => resp.json())
          );
          this.dataPage.lastKey++;
        }
      }
      for (let i in list) {
        list[i].forEach((product) => {
          product.key = newKey++;
        });
      }
    },
    // async addProductFromWarehouse(id, type) {
    //   let list = this.dataPage.productList;

    //   const product = await fetch(
    //     this.$connect + "pplication/dataProduct.php?warehouse_id=" + id
    //   );

    //   switch (type) {
    //     case "Linoleum":
    //       list.linoleum.push(product);
    //       this.dataPage.lastKey++;
    //       break;
    //     case "Plintusa":
    //       list.plinth.push(product);
    //       this.dataPage.lastKey++;
    //       break;
    //     case "accessories":
    //       list.accessories.push(product);
    //       this.dataPage.lastKey++;
    //       break;
    //     case "Porogi":
    //       list.doorstep.push(product);
    //       this.dataPage.lastKey++;
    //       break;
    //   }

    //   let newKey = 1;

    //   for (let i in list) {
    //     list[i].forEach((elem) => {
    //       elem.key = newKey++;
    //     });
    //   }
    // },
    async updateMain(data = []) {
      this.sortMainData = data;
      this.mainList = [];

      const f = await fetch(this.$connect + "mainList.php", {
        method: "POST",
        body: JSON.stringify(this.sortMainData),
      });
      const text = await f.json();
      text.forEach((el) => {
        this.mainList.push(el);
      });
    },
    sortingFor(e, column) {
      let order = e.target.dataset.order;
      if (order == "none") {
        e.target.dataset.order = "up";
        e.target.classList.add("up");
      } else if (order == "up") {
        e.target.dataset.order = "down";
        e.target.classList.remove("up");
        e.target.classList.add("down");
      } else if (order == "down") {
        e.target.dataset.order = "none";
        e.target.classList.remove("down");
      }
      order = e.target.dataset.order;
      switch (column) {
        case "city": {
          if (order == "none") {
            this.mainList.sort((a, b) => {
              return a.no - b.no;
            });
          } else if (order == "up") {
            this.mainList.sort((a, b) => {
              if (a.city > b.city) return 1;
              if (a.city == b.city) return 0;
              if (a.city < b.city) return -1;
            });
          } else if (order == "down") {
            this.mainList.sort((a, b) => {
              if (a.city < b.city) return 1;
              if (a.city == b.city) return 0;
              if (a.city > b.city) return -1;
            });
          }

          break;
        }
        case "status": {
          let status = this.status;
          if (order == "none") {
            this.mainList.sort((a, b) => {
              return a.no - b.no;
            });
          } else if (order == "up") {
            this.mainList.sort((a, b) => {
              if (
                status.indexOf(
                  status.filter((i) => {
                    return i.name == a.status;
                  })[0]
                ) >
                status.indexOf(
                  status.filter((i) => {
                    return i.name == b.status;
                  })[0]
                )
              )
                return 1;
              if (
                status.indexOf(
                  status.filter((i) => {
                    return i.name == a.status;
                  })[0]
                ) ==
                status.indexOf(
                  status.filter((i) => {
                    return i.name == b.status;
                  })[0]
                )
              )
                return 0;
              if (
                status.indexOf(
                  status.filter((i) => {
                    return i.name == a.status;
                  })[0]
                ) <
                status.indexOf(
                  status.filter((i) => {
                    return i.name == b.status;
                  })[0]
                )
              )
                return -1;
            });
          } else if (order == "down") {
            this.mainList.sort((a, b) => {
              if (
                status.indexOf(
                  status.filter((i) => {
                    return i.name == a.status;
                  })[0]
                ) <
                status.indexOf(
                  status.filter((i) => {
                    return i.name == b.status;
                  })[0]
                )
              )
                return 1;
              if (
                status.indexOf(
                  status.filter((i) => {
                    return i.name == a.status;
                  })[0]
                ) ==
                status.indexOf(
                  status.filter((i) => {
                    return i.name == b.status;
                  })[0]
                )
              )
                return 0;
              if (
                status.indexOf(
                  status.filter((i) => {
                    return i.name == a.status;
                  })[0]
                ) >
                status.indexOf(
                  status.filter((i) => {
                    return i.name == b.status;
                  })[0]
                )
              )
                return -1;
            });
          }
          break;
        }
        case "date": {
          if (order == "none") {
            this.mainList.sort((a, b) => {
              return a.no - b.no;
            });
          } else if (order == "up") {
            this.mainList.sort((a, b) => {
              let date_a =
                a.status == "Замер"
                  ? "measuring_date"
                  : a.status == "Доставка" || a.status == "Завершённый"
                  ? "delivery_date"
                  : "expectation";
              let date_b =
                b.status == "Замер"
                  ? "measuring_date"
                  : b.status == "Доставка" || b.status == "Завершённый"
                  ? "delivery_date"
                  : "expectation";
              if (a[date_a] > b[date_b] || b[date_b] == undefined) return 1;
              if (a[date_a] == b[date_b]) return 0;
              if (a[date_a] < b[date_b] || a[date_a] == undefined) return -1;
            });
          } else if (order == "down") {
            this.mainList.sort((a, b) => {
              let date_a =
                a.status == "Замер"
                  ? "measuring_date"
                  : a.status == "Доставка" || a.status == "Завершённый"
                  ? "delivery_date"
                  : "expectation";
              let date_b =
                b.status == "Замер"
                  ? "measuring_date"
                  : b.status == "Доставка" || b.status == "Завершённый"
                  ? "delivery_date"
                  : "expectation";
              if (a[date_a] < b[date_b] || a[date_a] == undefined) return 1;
              if (a[date_a] == b[date_b]) return 0;
              if (a[date_a] > b[date_b] || b[date_b] == undefined) return -1;
            });
          }

          break;
        }
      }
    },
    async addTab(no, data) {
      switch (no) {
        case "new":
          data = {
            seved: false,
            nick: "",
            initials: "",
            status: "Заявка",
            toc: "Авито",
            number: "",
            note: "",
            city: "",
            street: "",
            house: "",
            corpus: "",
            entrance: "",
            flat: "",
            no: 0,
            square: "0.00",
            weight: "0.00",
            sum: "0.00",
            purchase: "0.00",
            lastKey: 0,
            productList: {
              linoleum: [],
              plinth: [],
              accessories: [],
              doorstep: [],
              related: [],
            },
          };

          break;
        default:
          data.number = this.$acceptNumber(data.number);
          data.seved = true;
          data.productList = {
            linoleum: [],
            plinth: [],
            accessories: [],
            doorstep: [],
          };
          data.productList = await fetch(
            this.$connect + "application/productList.php?id=" + no
          ).then((resp) => resp.json());
          break;
      }
      var tab = { no: no, data: data };
      var list = this.tabs.filter((t) => t.no != tab.no);
      if (list.length == this.tabs.length)
        if (this.tabs.length >= 5) {
          let app = this.$parent.$parent;
          app.modal = {
            active: true,
            text: "Добавлено максимальное количество вкладок",
            error: true,
          };
          return false;
        } else {
          this.tabs.push(tab);
          this.selectTab(tab);
        }
      else this.selectTab(tab);
    },
    selectTab(tab) {
      this.selectedTab = tab.no;
      switch (tab.no) {
        case "new":
          this.displayApp = true;
          break;
        default:
          this.displayApp = true;
      }
      if (this.selectedTab == "main") {
        this.displayMain = true;
        this.$router.push({ name: "Main" });
      } else {
        if (
          (this.$route.name == "App" && this.$route.params.id != tab.no) ||
          this.$route.name != "App"
        )
          this.$router.push({ name: "App", params: { id: tab.no } });
        this.displayMain = false;
        this.dataPage = tab.data;
      }
    },
  },
  created() {
    this.user = JSON.parse(localStorage.user);
    this.updateMain(this.sortMainData);
    this.getLists();
    if (localStorage.tabs) {
      let t = this.tabs;
      let tabs = JSON.parse(localStorage.tabs);
      tabs.forEach((i) => {
        t.push(i);
      });
    }

    if (this.$route.name == "App") {
      this.selectTab(this.tabs.filter((i) => i.no == this.$route.params.id)[0]);
    }
  },
  watch: {
    tabs: {
      deep: true,
      handler() {
        localStorage.tabs = JSON.stringify(this.tabs);
      },
    },
  },
};
</script>
<style lang="scss" src="./../../assets\styles\main.scss"></style>
<style lang="scss" src="./../../assets\styles\navbar.scss"></style>
